import React from 'react';
import { useHistory } from "react-router-dom";
import { withNamespaces } from 'react-i18next';

const SizeAlert = ({text, close, t}) => {

    return (
        <div className='alert'>
            <div className="alert__content">
          
                <div className="alert__textarea" style={{marginTop: "1rem"}}>
                    <p className="aciklamafirst" style={{marginBottom: "2rem"}}>
                        {t("YourSizeIs")} {text}
                    </p>
                </div>
                <div className="loginContainer__main-input-control">
                    <button type="button" className="btn btn-blue btn-shadow btn-start loginContainer__main--input-submit"onClick={() => {
                                   close(false)
                    }}>{t("permissionAccept")}</button>
                </div>
            </div>
        </div>
    );
};

export default withNamespaces()(SizeAlert);